.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.black-nav {
  display: flex;
  align-content: start;
  padding: 0 20px;
  background-color: black;
}

.black-nav h4 {
  color: white;
}

.list {
  text-align: start;
  padding: 0 20px;
  border-bottom: 1px grey solid;
}

.modal {
  margin-top: 20px;
  padding: 20px;
  background-color: #eee;
  text-align: left;
}

.title-area {
  display: flex;
  flex-direction: row;
  gap: 5px;
}

.title-area > h4{
margin-bottom: 0;
}

.top-button {
  text-align: start;
  border-bottom: 1px grey solid;
  padding: 5px;
}

.bottom-bar {
  display: flex;
  gap: 5px;
  padding: 5px;
}